import { LOAD_MEMORY_LANE, LOAD_PUBLIC_MEMORY_LANE } from '../actions/types';
 
const initialState = {
    frndsMoments : [],
    publicMoments : []
}

export default (state : any = initialState, actions : any) => {
    switch(actions.type){
        case LOAD_MEMORY_LANE:
            return {
                ...state,
                frndsMoments : actions.payload
            }
        case LOAD_PUBLIC_MEMORY_LANE:
            return {
                ...state,
                publicMoments : actions.payload
            }
        default:
            return state;
    }
}

const loadMoments = (moments : any, state : any) => {
    let temp = state.moments;
    temp = [...temp, ...moments];
    return temp;
}