import React, { useState } from 'react'
import Profile from './profile/Profile'
import Header from '../header/Header'
import { Container } from 'react-bootstrap';
import PersonalMemoryLane from './personalMemoryLane/PersonalMemoryLane';
import FriendsList from './friendsList/FriendsList';
import ProtectedRoute from '../../utils/ProtectedRoute';
import { Switch } from 'react-router-dom';

function Main(props : any) {
    //const [profileOption, setProfileOption] = useState(0);
    console.log(props);
    const renderSelectedOption = () => {
      const profileOption = props.match.params.option;
      console.log(profileOption);
      switch(profileOption){
        case undefined:
          {
            return(
              <PersonalMemoryLane/>
            );
          } 
          break;
        case 'friends':
          {
            return(
              <FriendsList username={props.match.params.username}/>
            );
          } 
      }
    }

    return (
        <div>
          <Container>
            <Profile />
            {
              renderSelectedOption()
            }
            
          </Container>  
        </div>
    )
}

export default Main
