import React from 'react'
import FriendListStyle from './FriendList.module.css';
import { Link } from 'react-router-dom';

function FriendListItem(props : any) {
    const {firstname, lastname, username, profileImage} = props.friend;
    return (
        <div className={FriendListStyle.frndLstItm}>
            <img className={FriendListStyle.prfImg} src={profileImage}/>
            <Link to={`/${username}`} className={FriendListStyle.flNm}>{`${firstname} ${lastname ? lastname : ''}`}</Link>
            <Link to={`/${username}`} className={FriendListStyle.uN}>{username}</Link>
        </div>
    )
}

export default FriendListItem
