import React, { useEffect, useState } from 'react'
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import { getFriendsList } from '../../../actions/relationshipActions';
import FriendListItem from './FriendListItem';
import FriendListStyle from './FriendList.module.css';

function FriendsList(props : any) {
    
    const [friends, setFriends] = useState([]);

    useEffect(() => {
        props.getFriendsList(props.username);
    },[]);

    useEffect(() => {
        setFriends(props.friends);
    },[props.friends])

    return (
        <div className={FriendListStyle.wrpr}>
        {friends.map((friend : any) => {
            return (<FriendListItem friend = {friend}/>)
        })}        
        </div>
    )
}

const mapStateToProps = (state : any) => ({
    friends : state.relationship.friends
})

export default connect(mapStateToProps, {getFriendsList}) (FriendsList)
