import { GET_ERROR, GET_LOGIN_ERROR, GET_REGISTER_ERROR } from '../actions/types';

interface IError {
    message : string,
    login : string,
    register : string
}

const initialState : IError = {
    message : '',
    login : '',
    register : ''
}

export default (state : any = initialState, actions : any) => {
    switch(actions.type){
        case GET_ERROR:
            return {
                ...state,
                message : actions.payload
            }
        case GET_LOGIN_ERROR:
            return {
                ...state,
                login : actions.payload
            }
        case GET_REGISTER_ERROR:
            return {
                ...state,
                register : actions.payload
            }
        default:
            return state;            
    }
}