import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom';
import UserImg from "./UserImg";
import memoriesStyles from "./Memories.module.css";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import { faTrashAlt } from '@fortawesome/free-solid-svg-icons';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { deleteMemory } from '../../../../actions/memoryActions';

function Memory(props : any) {
    
    const memory = props.memory;

    const deleteMemory = () => {
        props.deleteMemory(props.momentId, memory.id);
    }

    return (
        <div>
            <div className={memoriesStyles.memTxt}>
                
                <div className={memoriesStyles.memUsrNm}>
                    <Link className={'no-a'} to={memory.owner.username}>
                        <UserImg src={memory.owner.profilePic}/> {`${memory.owner.firstname} ${(memory.owner.lastname) ? memory.owner.lastname : ''}`}
                    </Link>
                    {(memory.owner.username === props.username) &&
                    <FontAwesomeIcon className={memoriesStyles.delIco} icon={faTrashAlt} onClick={deleteMemory}/>}
                </div>
                
                <div  className={memoriesStyles.Mrywrpr}>
                    {memory.memory}
              </div>
            </div>
          </div>
    )
}

Memory.propTypes = {
    username : PropTypes.string.isRequired,
    deleteMemory : PropTypes.func.isRequired
}

const mapStateToProps = (state : any) => ({
    username : state.auth.username
})

export default connect(mapStateToProps, {deleteMemory})(Memory);
