import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import Moment from "../moment/Moment";
import { Row, Col, Container } from "react-bootstrap";
import memoryLaneStyles from "./MemoryLane.module.css";
import { fetchUserData, setQueryToLocal } from "../../../actions/authActions";
import { loadMemoryLane, loadPublicMemoryLane } from "../../../actions/memoryLaneActions";
import {
  showLoginPanel,
  showRegisterPanel
} from "../../../actions/headerActions";
import { connect, useStore } from "react-redux";
import PropTypes from "prop-types";
import LoginModel from "../../auth/LoginModel";
import RegisterModel from "../../auth/RegisterModel";
import UnAuthenticated from "../unauthenticated/UnAuthenticated";

function MemoryLane(props: any) {
  const history = useHistory();

  const [frndsMoments, setFrndsMoments] = useState([]);
  const [publicMoments, setPublicMoments] = useState([]);
  const [frndsMmt, setFrndsMmt] = useState(true);
  //const [memoryLaneLoading, setMemoryLaneLoading] = useState(false);

  useEffect(() => {
    if (!props.auth.isAuth) {
      const paramString: string = props.location.search;
      const params = new URLSearchParams(paramString);
      console.log(params.get("do"));
      if (params.get("success") && params.get("token")) {
        props.setQueryToLocal(params.get("token"), history);
      } else if (params.get("do")) {
        if (params.get("do") === "signin") {
          props.showLoginPanel();
        } else if (params.get("do") === "register") {
          props.showRegisterPanel();
        }
      }
    } else {
      //setMemoryLaneLoading(true);
      //console.log('init call '+memoryLaneLoading);
      props.loadMemoryLane();
    }
  }, [props.auth.isAuth]);

  useEffect(() => {
    
    const tArr: Array<any> = Array.from(props.frndsMoments.values());
    // console.log(tArr);
    // console.log('inside out '+memoryLaneLoading);
    // if(tArr.length === 0 && memoryLaneLoading){
    //   console.log('inside true');
    //   toggleLaneType(1);
    //   setMemoryLaneLoading(false);
    //   return;
    // }
    //setMemoryLaneLoading(false);
    tArr.sort(sortByCreationDt());
    setFrndsMoments(tArr);
  }, [props.frndsMoments]);

  useEffect(() => {
    const tArr: Array<any> = Array.from(props.publicMoments.values());
    tArr.sort(sortByCreationDt());
    setPublicMoments(tArr);
  }, [props.publicMoments]);

  const toggleLaneType = (type : number) => {
    if(type === 0){
      if(frndsMmt) return;
      //setMemoryLaneLoading(true);
      props.loadMemoryLane();
      setFrndsMmt(true);
    }else{
      if(!frndsMmt) return;
      props.loadPublicMemoryLane();
      setFrndsMmt(false);
    }
  }

  const sortByCreationDt = () => {
    return (a: any, b: any) => {
      let aDate = new Date(a.createdAt);
      let bDate = new Date(b.createdAt);
      if (aDate.getTime() < bDate.getTime()) {
        return 1;
      }
      if (aDate.getTime() > bDate.getTime()) {
        return -1;
      }
      return 0;
    };
  };

  const renderMain = () => {
    if(props.auth.isAuth){
      return (
        <React.Fragment>
          <div className={memoryLaneStyles.fxTp}>
          <div className={memoryLaneStyles.btnWrpr}>
              <div className={frndsMmt ? memoryLaneStyles.laneSlctd : memoryLaneStyles.laneBtn} onClick={() => toggleLaneType(0)}>Friends</div><div>&nbsp;</div>
              <div className={!frndsMmt ? memoryLaneStyles.laneSlctd : memoryLaneStyles.laneBtn} onClick={() => toggleLaneType(1)}>Public</div>
          </div>
        </div>
        <div className={memoryLaneStyles.memoryLane}>{renderMoments()}</div>
        </React.Fragment>
      )
    }else{
      return (
        <UnAuthenticated/>
      )
    }
  }

  const renderMoments = () => {
    if(frndsMmt){
      return frndsMoments.map((moment : any) => (
        <Row key={moment.id}>
          <Col key={moment.id}>
            <Moment key={moment.id} moment={moment} />
          </Col>
        </Row>
      ));
    }else{
      return publicMoments.map((moment : any) => (
        <Row key={moment.id}>
          <Col key={moment.id}>
            <Moment key={moment.id} moment={moment} />
          </Col>
        </Row>
      ));
    }
  };

  return (
    <React.Fragment>
      <Container>
        {renderMain()}
      </Container>
      <LoginModel show={false} />
      <RegisterModel show={false} />
    </React.Fragment>
  );
}

MemoryLane.propTypes = {
  fetchUserData: PropTypes.func.isRequired,
  setQueryToLocal: PropTypes.func.isRequired,
  showLoginPanel: PropTypes.func.isRequired,
  showRegisterPanel: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  loadMemoryLane: PropTypes.func.isRequired,
  loadPublicMemoryLane: PropTypes.func.isRequired,
  frndsMoments: PropTypes.array.isRequired
};

const mapStateToProps = (state: any) => ({
  auth: state.auth,
  frndsMoments: state.memoryLane.frndsMoments,
  publicMoments: state.memoryLane.publicMoments,
});

export default connect(mapStateToProps, {
  fetchUserData,
  setQueryToLocal,
  showLoginPanel,
  showRegisterPanel,
  loadMemoryLane,
  loadPublicMemoryLane
})(MemoryLane);
